.businessDetails {
  width: 650px;
  height: max-content;
  padding: 30px;
  margin: 25px auto 25px auto;

  background: #ffffff;
  border-radius: 10px;
}
.pageTitle {
  font-family: "Roboto";
  font-style: normal;
  font-weight: bold;
  font-size: 1.5vw;
  line-height: 18px;
  text-align: left;
  margin-left: 2.3%;

  color: #24272c;
}
.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: max-content;
  margin-left: auto;
  margin-right: auto;
  margin-left: 85%;
  cursor: pointer;
}
