select {
  font-size: 0.9rem;
  padding: 2px 5px;
  margin-right: 10px;
  height: 60px;
  background-color: #f2f3f7;
  border-color: #f2f3f7;
  border-radius: 8px;
}
select:focus {
  outline: none;
}
.Container {
  display: flex;
  overflow: hidden;

  .leftContainer {
    width: 29%;
  }
  .rightContainer {
    width: 71%;
    background: #f4f6f9;
    box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.15);
    border-radius: 16px;
    height: calc(100vh - 30px);
    margin-top: 15px;
    padding: 2%;

    .content {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;
      .button {
        display: flex;
        justify-content: center;
        align-items: center;
        width: max-content;
        margin-left: auto;
        margin-right: auto;
        margin-left: 85%;
        cursor: pointer;
      }
      .businessDetails {
        width: 650px;
        height: max-content;
        padding: 30px;
        margin: 25px auto 25px auto;

        background: #ffffff;
        border-radius: 10px;
      }
    }
    .pageTitle {
      font-family: "Roboto";
      font-style: normal;
      font-weight: bold;
      font-size: 1.5vw;
      line-height: 18px;
      text-align: left;
      margin-left: 2.3%;

      color: #24272c;
    }
  }
}
//header mobile
.MobileContainer {
  width: 100%;
  overflow: hidden;

  .header {
    width: 100%;
    display: flex;
    align-items: center;
    height: auto;
    padding: 10px;
    .headerTitle {
      font-family: "Roboto";
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      line-height: 28px;

      color: #24272c;
    }
    .businessDetails {
      margin-top: 20px;
    }
  }
}
